html, body, #root, .App {
  height: 100%;
}

.loginform {
  margin: 0 auto;
  text-align: center;
}
.tutorial {
  width: 100%;
  height: 100%;
  background-color: rgb(111, 189, 222);
}

.tutorial_stepper {
  background-color: rgb(250, 250, 250);
  border-radius: 10px;
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 500px;
  /* height: 500px; */
  text-align: center;
}

